/************************************************navbar style*******************************************************************/

.css-11b3ww9-MuiPaper-root-MuiAppBar-root {
  background-color: white !important;
  box-shadow: 0px 2px 2px lightgray !important;
  display: flex !important;
  justify-content: space-between;
  align-items: center;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root img {
  width: 250px;
  margin-left: 20px;
}

.css-11b3ww9-MuiPaper-root-MuiAppBar-root a , header a {
  color: #757575 !important;
  padding: 10px !important;
  display: inline-block !important;
  text-decoration: none !important;
  transition: 1s !important;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root a:hover , header a:hover {
  color: #ef3575 !important;
  cursor: pointer !important;
  transition: 1s;
}
.css-11b3ww9-MuiPaper-root-MuiAppBar-root a:active , header a:active {
  color: #ef3575 !important;
}

/* ***********************************************************footer style **************************************************************/
.footer {
  padding: 70px 40px;
}
.footer a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}
.icons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}
.icons a {
  color: #293e5a;
  display: flex;
  align-items: center;
  align-content: center;
  text-decoration: none;
  padding: 3px 6px;
  padding-top: 6px;
  display: inline-block;
  border-radius: 50%;
  background-color: white;
}
/* background */

.home-parent {
  position: relative !important;
}
.h-parent  {
  position: relative !important;
}
.h-parent .home {
  min-height: 80vh !important;
  border-radius: 0px 0px 50px 50px !important;

}
.calling .home {
  width: 95vw !important;
  margin: auto !important;
  height: 95vh !important;
  border-radius: 0px 0px 50px 50px !important;
object-fit: fill !important;
  display: flex !important;
  align-items: center;
  /* position: relative; */
}
.css-i4bv87-MuiSvgIcon-root {
  width: 15px !important;
  height: 15px !important;
}
.h-parent .home .parent {
  padding: 70px 50px;
  border-radius: 50px;
}
.esim-parent .home, .virtual-parent .home , .calling .home{
  display: flex !important;
  align-items: center;
  margin: auto !important;


 height: 95vh;
}
.calling .home {
  height: 100vh;

}
.h-parent .home {
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: 50vw;
  background-position: right;
  background-color: white;
  /* background-image: url("https://static.wixstatic.com/media/b54bb7_2db9d1257e214cf88006d6208ebea815~mv2.png/v1/fill/w_855,h_489,al_c,q_90,usm_0.66_1.00_0.01,enc_auto/Untitled-1.png"); */
}
.home .parent span {
  display: block;
  margin-bottom: 20px;
}
.home h1 {
  font-size: 36px;
  font-weight: bold;
}
.home form input {
  display: block;
  padding: 10px;
  padding-left: 35px;
  border-radius: 10px;
  border-color: greenyellow;
  border-style: solid;
  min-width: 60%;
  margin-top: 20px;
}
.esim-parent .home .im , .calling .im , .virtual-parent .im{
  width: 93vw;
  object-position: 70% 50%;


  padding:  75px 0px 0px 0px;
  border-radius: 0px 0px 50px 50px !important;
  margin: auto !important;
  height: 100% !important;
  object-fit: cover;



}
  



.virtual-parent .im {
  height:auto;
  margin: auto !important;
  object-fit: cover;
  object-position: -0% ;
  width: 100%;

  border-radius: 0px 0px 50px 50px !important;


}
.home .left,
.meet .left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  /* height: 300px; */
  animation: abs 1 1s;
  width: 80px;
}
.home .right,
.meet .right {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(0%) scale(-1);
  /* height: 300px; */
  width: 80px;
  animation: abs 1 1s;


}

.css-xnhscb-MuiCardMedia-root {
  min-width: 350px;
}
 @keyframes abs {
  from {
    width: 0px;
    opacity: 0;
  }
  to {
    opacity: 1;
    width: 80px;
  }
 }
.virtual-parent {
  position: relative !important;
}
