}
@media only screen and (max-width: 900px) {

  .esim-parent .home, .virtual-parent .home , .calling .home{
    display: flex !important;
    align-items: center;
    margin: auto !important;
  
  
    min-height: 65vh;
    max-height: 80vh;
  }
  .h-parent .home .parent {
  padding: 20px;
}
  .links {
    transition: 1s;
    position: fixed;
    top: 0px;
    right: -100%;
    height: 100vh;
    width: 100%;
    display: flex;
    font-size: 25px;
    justify-content: center;
    align-items: center;

    background-color: white;
  }
  .links .nav {
    flex-direction: column;
  }
  .links a {
    color: black !important;
    text-align: center !important;
    display: block !important;
  }
  .links a:nth-child(4) {
    color: #757575 !important;
  }
  .links img {
    display: block;
    margin-top: 100px;
  }
  .home {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .home .parent ,   #parent  {
    transform: translateX(0px) !important;
    width: 90% !important;
    /* position: absolute; */
  }
  .virtual-parent {
    position: relative;
  }

 
  
  .home .parent input {
    min-width: 80%;
  }
  .home .left,
  .meet .left {
    height: 200px;
    width: 50px;
  }
  .home .right,
  .meet .right {
    width: 50px;

    height: 200px;
  }
}

.hover:hover {
  background-color: white !important;
  color: black;
  border: 2px solid black;
  transition: 1s;
}
